import { notFound } from 'next/navigation';
import { mockVehicles } from '@/data/mockData';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';

import {
  ArrowLeft,
  Calendar,
  DollarSign,
  AlertTriangle,
  Clock,
  Package,
  Building,
  FileText,
  MapPin,
  User
} from 'lucide-react';
import Link from 'next/link';

interface EquipmentPageProps {
  params: {
    id: string;
  };
}

export default function EquipmentPage({ params }: EquipmentPageProps) {
  // Find equipment across all vehicles
  let equipment = null;
  let parentVehicle = null;

  for (const vehicle of mockVehicles) {
    const found = vehicle.devices.find(d => d.id === params.id);
    if (found) {
      equipment = found;
      parentVehicle = vehicle;
      break;
    }
  }

  if (!equipment || !parentVehicle) {
    notFound();
  }

  const now = new Date();
  const isOverdue = equipment.nextMaintenanceDate < now;
  const daysUntilDue = Math.ceil((equipment.nextMaintenanceDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24));

  // Estimate maintenance cost based on criticality
  const estimatedCost = equipment.criticality === 'critical' ? 1500 :
    equipment.criticality === 'high' ? 1000 :
      equipment.criticality === 'medium' ? 600 : 300;

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'overdue': return <Badge variant="destructive">Overdue Maintenance</Badge>;
      case 'due': return <Badge variant="warning">Due Soon</Badge>;
      case 'upcoming': return <Badge variant="default">Upcoming</Badge>;
      case 'completed': return <Badge variant="success">Up to Date</Badge>;
      default: return <Badge variant="secondary">{status}</Badge>;
    }
  };

  const getCriticalityBadge = (criticality: string) => {
    switch (criticality) {
      case 'critical': return <Badge variant="destructive">Critical</Badge>;
      case 'high': return <Badge variant="warning">High Priority</Badge>;
      case 'medium': return <Badge variant="default">Medium Priority</Badge>;
      case 'low': return <Badge variant="secondary">Low Priority</Badge>;
      default: return <Badge variant="secondary">{criticality}</Badge>;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <Link
            href={`/vehicle/${parentVehicle.id}`}
            className="inline-flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors mb-4"
          >
            <ArrowLeft className="h-5 w-5" />
            Return to Vehicle: {parentVehicle.name}
          </Link>
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">{equipment.name}</h1>
              <p className="mt-1 text-sm text-gray-600">
                Equipment ID: {equipment.id}
              </p>
            </div>
            <div className="flex items-center gap-2">
              {getStatusBadge(equipment.status)}
              {getCriticalityBadge(equipment.criticality)}
            </div>
          </div>
        </div>

        {/* Main Information Card */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Equipment Details</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <Package className="h-4 w-4" />
                    Equipment Type
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.type.replace(/_/g, ' ').toUpperCase()}</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <Building className="h-4 w-4" />
                    Manufacturer
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.manufacturer || 'Not specified'}</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <FileText className="h-4 w-4" />
                    Model Number
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.model || 'Not specified'}</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <FileText className="h-4 w-4" />
                    Serial Number
                  </label>
                  <p className="mt-1 text-gray-900 font-mono">{equipment.serialNumber}</p>
                </div>
              </div>

              <div className="space-y-4">
                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <MapPin className="h-4 w-4" />
                    Installation Location
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.location}</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <AlertTriangle className="h-4 w-4" />
                    Criticality Level
                  </label>
                  <p className="mt-1 text-gray-900 capitalize">{equipment.criticality}</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <Clock className="h-4 w-4" />
                    Maintenance Interval
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.maintenanceIntervalDays} days</p>
                </div>

                <div>
                  <label className="text-sm font-medium text-gray-700 flex items-center gap-2">
                    <User className="h-4 w-4" />
                    Assigned Technician
                  </label>
                  <p className="mt-1 text-gray-900">{equipment.assignedContacts[0]?.name || 'Not assigned'}</p>
                </div>
              </div>
            </div>

            <div className="mt-6 pt-6 border-t border-gray-200">
              <label className="text-sm font-medium text-gray-700">Description</label>
              <p className="mt-1 text-gray-900">{equipment.description}</p>
            </div>
          </CardContent>
        </Card>

        {/* Maintenance Information */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Card>
            <CardHeader>
              <CardTitle className="text-base">Last Maintenance</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center gap-3">
                <Calendar className="h-8 w-8 text-gray-400" />
                <div>
                  <p className="text-2xl font-bold text-gray-900">
                    {equipment.lastMaintenanceDate?.toLocaleDateString() || 'Never'}
                  </p>
                  <p className="text-sm text-gray-600">
                    {equipment.lastMaintenanceDate ?
                      `${Math.floor((now.getTime() - equipment.lastMaintenanceDate.getTime()) / (1000 * 60 * 60 * 24))} days ago` :
                      'No maintenance record'}
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Next Scheduled Maintenance</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center gap-3">
                {isOverdue ?
                  <AlertTriangle className="h-8 w-8 text-red-600" /> :
                  <Clock className="h-8 w-8 text-blue-600" />
                }
                <div>
                  <p className={`text-2xl font-bold ${isOverdue ? 'text-red-600' : 'text-gray-900'}`}>
                    {equipment.nextMaintenanceDate.toLocaleDateString()}
                  </p>
                  <p className={`text-sm ${isOverdue ? 'text-red-600' : 'text-gray-600'}`}>
                    {isOverdue ? `${Math.abs(daysUntilDue)} days overdue` : `in ${daysUntilDue} days`}
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Estimated Maintenance Cost</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center gap-3">
                <DollarSign className="h-8 w-8 text-green-600" />
                <div>
                  <p className="text-2xl font-bold text-gray-900">
                    QAR {estimatedCost.toLocaleString()}
                  </p>
                  <p className="text-sm text-gray-600">Per maintenance cycle</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Parent Vehicle Information */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Parent Vehicle Information</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="text-sm font-medium text-gray-700">Vehicle Name</label>
                <p className="mt-1 text-gray-900">{parentVehicle.name}</p>
              </div>
              <div>
                <label className="text-sm font-medium text-gray-700">Vehicle Model</label>
                <p className="mt-1 text-gray-900">{parentVehicle.model}</p>
              </div>
              <div>
                <label className="text-sm font-medium text-gray-700">Registration Number</label>
                <p className="mt-1 text-gray-900 font-mono">{parentVehicle.registrationNumber}</p>
              </div>
              <div>
                <label className="text-sm font-medium text-gray-700">License Plate</label>
                <p className="mt-1 text-gray-900 font-mono">{parentVehicle.licensePlate}</p>
              </div>
            </div>
            <div className="mt-6">
              <Link
                href={`/vehicle/${parentVehicle.id}`}
                className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-700 hover:bg-blue-800"
              >
                View Complete Vehicle Details
              </Link>
            </div>
          </CardContent>
        </Card>

        {/* Assigned Contact */}
        {equipment.assignedContacts && equipment.assignedContacts.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle>Assigned Technician Contact</CardTitle>
            </CardHeader>
            <CardContent>
              {equipment.assignedContacts.map((contact) => (
                <div key={contact.id} className="flex items-start gap-4">
                  <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center text-blue-700 font-bold text-lg">
                    {contact.name.charAt(0)}
                  </div>
                  <div>
                    <p className="font-semibold text-gray-900">{contact.name}</p>
                    <p className="text-sm text-gray-600">{contact.role}</p>
                    {contact.email && (
                      <p className="text-sm text-gray-600 mt-1">
                        <span className="font-medium">Email:</span> {contact.email}
                      </p>
                    )}
                    {contact.phone && (
                      <p className="text-sm text-gray-600">
                        <span className="font-medium">Phone:</span> {contact.phone}
                      </p>
                    )}
                  </div>
                </div>
              ))}
            </CardContent>
          </Card>
        )}
      </main>
    </div>
  );
}